.App {
  text-align: center;
}

/* Mapbox */
#container {
  display: -ms-flexbox;
  display: flex;
}
#map {
    position: fixed;
    width: 100%;
}
#features {
    z-index: 9999;
    width: 100%;
    margin-left: 0%;
    font-family: sans-serif;
    overflow-y: scroll;
    color: white;
    padding:  0px 5vw;
    background-color: rgba(255, 255, 255, 0);
}
.marker {
    width: 15px;
    height: 15px;
    display: block;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    padding: 0;
    background-image: -webkit-linear-gradient(330deg, #f6d365 0%, #fda085 100%);
    background-image: -o-linear-gradient(330deg, #f6d365 0%, #fda085 100%);
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
    -webkit-box-shadow: 2px 3px 10px rgba(255, 160, 160, 0.75);
            box-shadow: 2px 3px 10px rgba(255, 160, 160, 0.75);
    opacity: 0.25;
}
.marker.active {
    width: 25px;
    height: 25px;
    opacity: 1;
}



